import { request } from 'utils';

const prefix = '/api/categories';

export const getBuildings = ({
  label,
  baseId,
  noOfFloor,
  page,
  pageSize=10,
  orderBy,
  isPagingEnabled=true
} = {}) =>
  request({
    prefix,
    url: '/Building',
    method: 'GET',
    params: {
      label,
      baseId,
      noOfFloor,
      page,
      pageSize,
      orderBy,
      isPagingEnabled
    },
    data: {}
  });
  
export const getFloorByBuilding = ({
  buildingId,
  page,
  pageSize=10,
  orderBy,
  isPagingEnabled=true
} = {}) =>
  request({
    prefix,
    url: '/Building/GetBuildingFloorCollection',
    method: 'GET',
    params: {buildingId,page,pageSize,orderBy,isPagingEnabled},
    data: {}
  });





