import { request } from 'utils';

const prefix = '/api/categories';

export const getBuildingBases = ({
  label,
  page,
  pageSize=10,
  orderBy,
  isPagingEnabled=true
} = {}) =>
  request({
  prefix,
  url: '/BuildingBase',
  method: 'GET',
  params: {
    label,
    page,
    pageSize,
    orderBy,
    isPagingEnabled
  },
  data: {}
});



